/**
 * Study object for a DPS Study data
 *
 * @module Slide
 */

 export default class Slide {
    constructor(slideId, slideInfo) {
        this._slideId = slideId;

        this._fileName = '';
        this._fileSize = 50;
        this._createdDate = '';
        this._createdTime = '';
        this._markerType = '';

        this._arrKeyImages = [];

        if(slideInfo != null) {
            this._fileName = slideInfo.fileName;
            this._fileSize = slideInfo.fileSize;
            this._createdDate = slideInfo.createdDate;
            this._createdTime = slideInfo.createdTime;
            this._markerType = slideInfo.markerTypeName;
        }
    }

    setListKeyImages(keyImagesInfo) {
        if(keyImagesInfo == undefined || keyImagesInfo.length == 0)
            return;

        this._arrKeyImages = [];

        let len = keyImagesInfo.length;
        for(let i = 0; i < len; i++) {
            let obj = {};
            Object.assign(obj, keyImagesInfo[i]);
            this._arrKeyImages.push(obj);
        }
    }
}