var slideViewer = null;
var mainStudy = null;
var mainContainerId = '';

export function init(containerId) {
    mainContainerId = containerId;
    getListSlides();

    //prepare events
    $(".one-at-a-time").click(function (e) {
        let thisBtn = e.currentTarget;
        let activeFunc = new Function($(thisBtn).attr("active"));
        let deactiveFunc = new Function($(thisBtn).attr("deactive"));
    
        if ($(thisBtn).hasClass("selected")) {
            deactiveFunc();
            $(thisBtn).removeClass("selected");
            let pointer = $($("#tool_pointer"));
            let pointerActive = new Function(pointer.attr("active"));
            pointerActive();
            pointer.addClass("selected");
        } else {
            let currentActive = $($(".one-at-a-time.selected")[0]);
            let currentDeactiveFunc = new Function(currentActive.attr("deactive"));
            currentActive.removeClass("selected");
    
            currentDeactiveFunc();
            activeFunc();
            $(thisBtn).addClass("selected");
        }
    });

    //show loading
    $('#slide-viewer-loading').css({ display: 'block' });
}

export function getListSlides() {
    // $.ajax({
    //     url: GlobalVars.domain + Constants.URL_GET_LIST_SLIDES + GlobalVars.studyId,
    //     method: 'GET',
    //     success: function (res) {
    //         console.log(res);
    //         if (res.d.IsValid) {
    //             _initStudy(res.d);

    //             //hide loading
    //             $('#slide-viewer-loading').css({ display: 'none' });
    //         }
    //     }
    // });

    parent.getListSlides(GlobalVars.studyId, function (res) {
        console.log('getListSlides res:', res);
        _initStudy(res);

        //hide loading
        $('#slide-viewer-loading').css({ display: 'none' });
    });
}

function _initStudy(studyInfo) {
    if(mainStudy != null)
        mainStudy.dispose();

    mainStudy = new Study(GlobalVars.studyId, studyInfo);

    //show list slides & viewer
    if(mainStudy._arrSlides.length > 0) {
        //show viewer for first slideId
        let slide = mainStudy._arrSlides[0];
        GlobalVars.slideId = slide._slideId;
        showSlide(GlobalVars.slideId);

        //show list slides
        _showListSlides();
    }
    else {
        //TODO: show message empty study 
    }
}

function _showListSlides() {
    let container = $("#slides-box");
    container.html('');

    let slide, slideDiv;
    let len = mainStudy._arrSlides.length;
    for(let i = 0; i < len; i++) {
        slide = mainStudy._arrSlides[i];
        let slideId = slide._slideId;
        slideDiv = $('<div>').addClass('image-container').attr('id', slideId);
        let sTitle = 'Tn file: ' + slide._filename + '<br/>Kch thc: ' + slide._fileSize + ' MB<br/>Ngy to: ' + slide._createdTime;
        slideDiv.attr('title', sTitle);

        //event
        slideDiv.on('click', function(ev) {
            console.log('onClick, ' + slideId);
            showSlide(slideId);
        });

        //thunmbnail
        let thumb = $('<div>').addClass('slide-thumbnail');
        thumb.html('<img src="' + GlobalVars.domainSlide + Constants.URL_SLIDE_THUMBNAIL + slideId + '" />');
        slideDiv.append(thumb);

        //label
        let label = $('<div>').addClass('slide-label');
        label.html('<img src="' + GlobalVars.domainSlide + Constants.URL_SLIDE_LABEL + slideId + '" />');
        slideDiv.append(label);

        //slide title (pp nhuom)
        let title = $('<div>').addClass('slide-title');
        title.html(slide._markerType);
        slideDiv.append(title);

        container.append(slideDiv);
    }
}

export function getListKeyImages(slideId) {
    // $.ajax({
    //     url: GlobalVars.domain + Constants.URL_GET_KEYIMAGES + slideId,
    //     method: 'GET',
    //     success: function (res) {
    //         console.log(res);
    //         if (res.d.IsValid) {
    //             _showListKeyImages(res.d.JsonData);
    //         }
    //     }
    // });

    parent.getListKeyImages(slideId, function (res) {
        console.log(res);
        _showListKeyImages(slideId, res);
    });
}

function _showListKeyImages(slideId, keyImagesInfo) {
    let container = $("#images-box");
    container.html('');

    let slide = mainStudy.getSlide(slideId);
    if(slide == null) {
        console.log('DEV: Why slide null??? SlideId: ' + slideId);
        return;
    }

    //set key images from keyimagesInfo
    slide.setListKeyImages(keyImagesInfo);

    //show in page
    let keyImg, keyImgDiv;
    let len = slide._arrKeyImages.length;
    for(let i = 0; i < len; i++) {
        keyImg = slide._arrKeyImages[i];
        keyImgDiv = $('<div>').addClass('keyimage-container').attr('id', keyImg.Id);
        // let sTitle = 'Tn file: ' + slide._filename + '<br/>Kch thc: ' + slide._fileSize + ' MB<br/>Ngy to: ' + slide._createdTime;
        // slideDiv.attr('title', sTitle);

        //thunmbnail
        let thumb = $('<div>').addClass('keyimage-thumbnail');
        thumb.html('<img src="' + GlobalVars.domain + Constants.URL_KEYIMAGE_PATH + '/' + keyImg.Id + '" />');
        keyImgDiv.append(thumb);

        //details
        let label = $('<div>').addClass('keyimage-detail');
        label.html('<div class="keyimage-title">' + keyImg.Title + '</div><div class="keyimage-desc">' + keyImg.Note + '</div>');
        keyImgDiv.append(label);

        container.append(keyImgDiv);
    }
}

export function showSlide(slideId) {
    console.log('showSlide ' + slideId);
    GlobalVars.slideId = slideId;

    if(slideViewer == null) {
        slideViewer = new SlideViewer(mainContainerId, slideId);
    }
    else {
        slideViewer.openNewSlide(slideId);
    }
    // slideViewer.dispose();

    //get list key images
    getListKeyImages(slideId);
}

export function toggleRightPanel() {
    let rightPane = $("#right-panel-container");
    if (rightPane.hasClass("hided")) {
        rightPane.removeClass("hided");
        rightPane.animate({ right: "0px" }, 240, "swing", function () {
            rightPane.css("position", "relative");
            $(".left-piece .inner-icon")[0].style.transform = "rotate(0deg)";
        });
    } else {
        rightPane.addClass("hided");
        rightPane.animate({ right: "-240px" }, 240, "swing", function () {
            rightPane.css("position", "absolute");
            $(".left-piece .inner-icon")[0].style.transform = "rotate(180deg)";
        });
    }
}

export function openReport() {
    parent.fakeLoadReport(GlobalVars.slideId, true);
}

export function fitByRatio(ratio = 0) {
    if(ratio == 0)
        slideViewer.fitAuto();
    else {
        let pointer = slideViewer._mainViewer.viewport.getCenter();
        slideViewer.fitTo(pointer, ratio, true);
    }
}

