////////////////////////////////////////////////////
// User settings
////////////////////////////////////////////////////
export function showUserSettings() {
    if(GlobalVars.dlgUserSettings == null) {
        GlobalVars.dlgUserSettings = $("#dlgSlideSettings").kendoWindow({
            minWidth: 380,
            width: 400,
            height: 300,
            title: "Ci t DPS Viewer",
            animation: false,
            visible: false,
            close : function onClose(e) {
                // closeUserSettings();
            },
            actions: [
                "Close"
            ],
        }).data("kendoWindow");
    }

    //show settings values
    if(GlobalVars.userSettings.UseKeyImageConstraint == true) {
        $('#keyimage-use-constraint').prop("checked", true);
        $("#keyimage-ratio-width").removeAttr('disabled');
        $("#keyimage-ratio-height").removeAttr('disabled');
    }
    else {
        $('#keyimage-use-constraint').prop("checked", false);
        $("#keyimage-ratio-width").attr('disabled','disabled');
        $("#keyimage-ratio-height").attr('disabled','disabled');
    }
    $("#keyimage-ratio-width").val(GlobalVars.userSettings.KeyImageConstraintWidth);
    $("#keyimage-ratio-height").val(GlobalVars.userSettings.KeyImageConstraintHeight);
    $("#keyimage-pixel-width").val(GlobalVars.userSettings.KeyImagePixelWidth);

    GlobalVars.dlgUserSettings.center().open();
}

export function closeUserSettings() {
    if(GlobalVars.dlgUserSettings != null) {
        GlobalVars.dlgUserSettings.close();
    }
}

export function saveUserSettings() {
    $.ajax({
        url: '/api/keyimagesettings/',
        method: 'PUT',
        contentType: 'application/json',
        data: JSON.stringify({
            useConstraint: document.getElementById('keyimage-use-constraint').checked,
            constraintWidth: document.getElementById('keyimage-ratio-width').value,
            constraintHeight: document.getElementById('keyimage-ratio-height').value,
        }),
        success: function (e) {
            if (e.d.IsValid) {
                selection.useConstraint = document.getElementById('keyimage-use-constraint').checked;
                selection.constraintWidth = parseInt(document.getElementById('keyimage-ratio-width').value);
                selection.constraintHeight = parseInt(document.getElementById('keyimage-ratio-height').value);
            } else {
                alert('Tht bi.');
            }
        },
        error: function (e) {
            alert('Tht bi.');
        }
    });
    GlobalVars.dlgUserSettings.close();
}

export function setActiveConstraintField(checked) {
    if (!checked)
        $('.keyimage-constraint-field').attr('disabled', 'true');
    else
        $('.keyimage-constraint-field').removeAttr('disabled');
}

export function clickChildCheckBox(elm) {
    $(elm).find('[type=checkbox]').click();
}

