import Slide from "./slide";

/**
 * Study object for a DPS Study data
 *
 * @module Study
 */

 export default class Study {
    constructor(studyId, slidesInfo) {
        this._studyId = studyId;
        this._arrSlides = [];

        if(slidesInfo != null && slidesInfo.length > 0) {
            for(let i = 0; i < slidesInfo.length; i++) {
                let slide = new Slide(slidesInfo[i].id, slidesInfo[i]);
                this._arrSlides.push(slide);
            }
        }
    }

    getSlide(slideId) {
        for(let i = 0; i < this._arrSlides.length; i++) {
            if(this._arrSlides[i] != undefined && this._arrSlides[i]._slideId === slideId) {
                return this._arrSlides[i];
            }
        }
        return null;
    }
}